.com-header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 70px;
  background: #fff;
  padding: 0 40px 0 265px;
  z-index: 4;
}
.com-header .menuIcon {
  display: none;
}
.com-header .t-input {
  width: 230px;
  height: 38px;
  border-radius: 4px;
  background: rgba(243, 243, 249, 0.9);
  margin-left: 40px;
}
.com-header .t-input input {
  background: none;
}
.com-header .box {
  display: flex;
  justify-content: center;
  align-items: center;
}
.com-header .box .item {
  margin-left: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.com-header .ant-badge-count {
  width: 0.2rem;
  height: 0.2rem;
  background: #E34D59 !important;
  top: 0.05rem;
  right: 0.05rem;
}
.com-header .bg {
  width: 0.36rem;
  height: 0.36rem;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.3s;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.com-header .bg:hover {
  background: #F0F0FD;
}
.com-header .tip {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  overflow: hidden;
  background: pink;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.com-header .select-img {
  width: 0.2rem;
  height: 0.2rem;
  margin-right: 0.08rem;
  border-radius: 0.04rem;
}
.com-header #avatar .avatar {
  width: 32px;
  height: 32px;
  border-radius: 100%;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.com-header #avatar .ant-popover {
  top: 56px !important;
}
.com-header #avatar .ant-popover-inner {
  border-radius: 8px;
}
.com-header #avatar .ant-popover-arrow {
  display: none;
}
.com-header #avatar .ant-popover-inner-content {
  padding: 26px  30px;
}
.com-header #avatar .img {
  width: 72px;
  height: 72px;
}
.com-header .t-product {
  height: 40px;
  padding: 0 17px;
  background: #696AE6;
  color: #fff;
}
.com-header .ant-dropdown-menu {
  top: 14px;
  border-radius: 8px;
}
.com-header #product .ant-dropdown-menu {
  padding: 14px 18px 14px 10px;
  top: 12px;
  border-radius: 8px;
  color: #040E34;
}
.com-header #product .ant-dropdown-menu-item {
  border-radius: 4px;
  font-size: 13px;
  width: 132px;
  font-weight: 400;
}
.com-header #product .ant-dropdown-menu-item span,
.com-header #product .ant-dropdown-menu-item a {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.com-header #product .ant-dropdown-menu-item:hover {
  background: #F0F0FD;
  color: #5C5EE4;
}
.com-header .user-info .top {
  display: flex;
  margin-bottom: 25px;
}
.com-header .user-info .top .img {
  margin-right: 16px;
}
.com-header .user-info .name {
  font-size: 18px;
  color: #040E34;
  line-height: 1;
  margin: 17px 0 8px;
}
.com-header .user-info .price {
  font-size: 13px;
  color: #9696A3;
}
.com-header .user-info .info {
  font-size: 13px;
  display: flex;
  flex-direction: column;
}
.com-header .user-info .l-label {
  font-weight: bold;
  color: #040E34;
}
.com-header .user-info .r-txt {
  color: #9696A3;
}
.com-header .user-info .item {
  margin-left: 0;
  display: block;
  line-height: 1;
}
.com-header .user-info .item p {
  margin-bottom: 13px;
}
.com-header .user-info .logout {
  line-height: 40px;
  width: 228px;
  color: #5C5EE4;
  background: #F0F0FD;
  border: none;
  cursor: pointer;
  margin-top: 20px;
}
.com-header #language .ant-dropdown-menu {
  padding: 14px 18px 14px 10px;
}
.com-header #language .ant-dropdown-menu-item {
  border-radius: 4px;
  font-size: 13px;
  width: 132px;
  font-weight: 400;
}
.com-header #language .ant-dropdown-menu-item span,
.com-header #language .ant-dropdown-menu-item a {
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.com-header #language .ant-dropdown-menu-item:hover {
  background: #F0F0FD;
  color: #5C5EE4;
}
.com-header #language img {
  border-radius: 0.04rem;
}
.com-header #language .ant-dropdown-menu-title-content {
  display: flex;
  align-items: center;
}
.com-header #language .ant-dropdown-menu-item-disabled:hover {
  background: none;
  color: rgba(0, 0, 0, 0.25);
}
@media screen and (max-width: 992px) {
  .com-header {
    padding: 0 15px;
  }
  .com-header .menuIcon {
    font-size: 20px;
    display: inline-block;
  }
  .com-header .t-input {
    margin-left: 26px;
  }
}
@media screen and (max-width: 768px) {
  .com-header .t-input {
    width: 35px;
  }
}
