@import '~antd/dist/antd.css';
.ant-btn-primary,
.ant-btn-primary:hover,
.ant-btn-primary:focus {
  background: #25255B;
  border-color: #25255B;
}
.com-box {
  width: 100%;
  height: 100%;
}
.layout {
  height: 100%;
}
.empty {
  width: 100%;
  padding-top: 70px;
}
/* com-content */
.com-content {
  box-sizing: border-box;
  display: flex;
  padding: 30px 0;
  background: #FAFAFE;
}
.com-content .com-card {
  border-radius: 8px;
  width: 77.945619%;
  background: rgba(255, 255, 255, 0.9);
  height: 100%;
  box-shadow: 0 0 16px 0 rgba(137, 138, 228, 0.12);
  margin: 0 auto;
}
.com-mask {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  z-index: -1;
  top: 0;
  left: 0;
  display: none;
}
.site-layout {
  height: 100%;
  flex: 1;
  position: relative;
  margin-left: 265px;
}
.ant-dropdown-menu,
.ant-popover-inner {
  box-shadow: 0 3px 6px -4px rgba(105, 106, 230, 0.12), 0 6px 16px 0 rgba(105, 106, 230, 0.08), 0 9px 28px 8px rgba(105, 106, 230, 0.05);
}
@media screen and (max-width: 1600px) {
  .com-content .com-card {
    width: 90%;
  }
}
@media screen and (max-width: 992px) {
  .site-layout {
    margin-left: 0;
  }
  .com-mask.hide {
    display: block;
  }
  .com-header {
    padding: 0 15px;
  }
}
@media screen and (max-width: 768px) {
  .com-content {
    padding: 0.3rem 0;
  }
  .com-content .com-card {
    width: 100%;
    margin: 0 0.3rem;
  }
}
