.login {
  background: #fafafe;
  width: 100%;
  height: 100%;
  position: relative;
}
.login .l-bg {
  width: 2.17rem;
  height: 2.75rem;
  position: absolute;
  left: 0;
  bottom: 0;
  background: url(../../assets/images/bg-left.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 1;
}
.login .r-bg {
  width: 46.04%;
  max-width: 7rem;
  height: 60.9%;
  position: absolute;
  right: 4.74%;
  top: 19.9%;
  bottom: 19.9%;
  background: url(../../assets/images/bg-right.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 1;
}
.r-bg-color {
  width: 30%;
  max-width: 7rem;
  height: 100%;
  background: #f0f0fd;
  opacity: 0.6;
  border-radius: 0px;
  position: absolute;
  right: 0;
  bottom: 0;
}
.login .form {
  width: 5.3rem;
  /* height: 5.5rem; */
  position: absolute;
  padding: 0.5rem 0.4rem;
  top: 19.9%;
  left: 18.75%;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 0.2rem;
  box-shadow: 0 0 0.2rem 0.1rem rgba(105, 106, 230, 0.05);
  z-index: 10;
}
.login_title {
  font-size: 0.26rem;
  font-family: Source Han Sans CN;
  font-weight: bold;
  height: 0.3rem;
  line-height: 0.3rem;
  color: #040e34;
  margin-bottom: 0.1rem;
}
.form_item {
  height: 0.48rem;
  line-height: 0.48rem;
  /* padding: 0.07rem; */
  margin: 0.2rem 0 0.2rem 0;
  background: #f3f3f9;
  opacity: 1;
  border-radius: 0.04rem;
}

.form_item_code {
  width: 70%;
  /* height: 0.06rem; */
  height: 0.48rem;
  line-height: 0.48rem;
  /* margin-bottom: 40px; */
  background: #f3f3f9;
  opacity: 1;
  border-radius: 0.04rem;
}
.form_item_code input {
  width: "100%";
  border: "none";
  outline: "none";
  background-color: "#f3f3f9";
  outline: none;
}
.bottom_code {
  width: 0.96rem;
  height: 0.48rem !important;
  background: #42c2fb !important;
  opacity: 1;
  border-radius: 0.04rem !important;
  color: #fff !important;
  font-size: 0.14rem !important;
}
.flex {
  display: flex;
}
.just_between {
  justify-content: space-between;
}
.select_input {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 992px) {
}

@media screen and (max-width: 768px) {
  .login .form {
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
  }
  .bottom_code {
    height: 100% !important;
    width: 26%;
  }
  .phonelogin_btn {
    margin-top: 0.45rem;
  }
  .code_item .setcodebox {
    bottom: -0.35rem;
  }
  .login_btn {
    height: 0.6rem;
    line-height: 0.6rem;
  }
  .form_item {
    margin: 0.2rem 0 0.4rem 0;
  }
}
