.register_from {
  .ant-tabs-tab:hover {
    color: #000 !important;
  }
  .ant-tabs-tab-active {
    color: #000 !important;
    border-bottom-color: #5c5ee4 !important;
  }
  .ant-tabs-ink-bar {
    background-color: transparent !important;
  }
  .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
    color: #5c5ee4 !important;
  }
  .ant-tabs-top .ant-tabs-ink-bar-animated:before {
    content: " ";

    background-color: #5c5ee4;

    width: 100%;

    height: 2px;

    position: absolute;

    margin: auto;
  }
  .ant-tabs-nav-list {
    width: 100%;
    .ant-tabs-tab {
      width: 100%;

      .ant-tabs-tab-btn {
        width: 100%;
        text-align: center;
      }
    }
  }
}
@media screen and (max-width: 768px) {
  .login .form {
    height: 100% !important;
    width: 100%;
    top: 0;
    left: 0;
  }
  .register_btn {
    margin-top: 0.2rem !important;
  }
}

