.forget_form {
  height: auto !important;
  .ant-tabs-tab:hover {
    color: #000 !important;
  }
  .ant-tabs-tab-active {
    color: #000 !important;
    border-bottom-color: #5c5ee4 !important;
  }
  .ant-tabs-ink-bar {
    background-color: transparent !important;
  }
  .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
    color: #5c5ee4 !important;
  }
  .ant-tabs-top .ant-tabs-ink-bar-animated:before {
    content: " ";

    background-color: #5c5ee4;

    width: 100%;

    height: 2px;

    position: absolute;

    margin: auto;
  }
  .ant-tabs-nav-list {
    width: 100%;
    .ant-tabs-tab {
      width: 100%;

      .ant-tabs-tab-btn {
        width: 100%;
        text-align: center;
      }
    }
  }
}
.count_down {
  width: 96px;
  height: 48px;
  line-height: 48px;
  background: #ccc;
  opacity: 0.7;
  border-radius: 4px;
  font-size: 14px;
  text-align: center;
}

@primary-color: #5C5EE4;