@import "~antd/dist/antd.css";
html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Source Han Sans CN';
}
body.swal2-height-auto {
  height: 100% !important;
}
.swal2-popup {
  font-size: 0.16rem;
}
#root {
  position: fixed;
  width: 100%;
  height: 100%;
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px #2A2A48;
  -webkit-box-shadow: inset 0 0 2px #2A2A48;
  border-radius: 10px;
  background-color: #f5f5f5;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 2px #2A2A48;
  -webkit-box-shadow: inset 0 0 2px #2A2A48;
  background-color: #5c5ee4;
}
ul,
li {
  list-style: none;
}
.ellipse-one {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.ant-layout-content {
  background: #fafafe;
  overflow-y: auto;
}
.ant-input:hover {
  border-color: #5c5ee4;
}
.clear::after {
  content: "";
  display: table;
  clear: both;
}
.left {
  float: left;
}
.right {
  float: right;
}
.com-radius {
  border-radius: 4px;
}
.com-hover {
  transition: all 0.3s linear;
}
.com-hover:hover {
  background: #5c5ee4 !important;
  color: #fff !important;
}
.com-danger {
  background: #ffe2e5;
  border-radius: 0.08rem;
  padding: 0.24rem;
  width: 100%;
  font-size: 0.13rem;
  color: #f64e60;
}
.com-danger p {
  margin-bottom: 0;
  line-height: 1.5;
}
.com-danger .des {
  margin-top: 0.16rem;
  padding-left: 0.25rem;
}
.com-danger .tit {
  line-height: 1;
}
.com-add-btn {
  line-height: 0.4rem;
  padding: 0 0.16rem;
  color: #fff;
  background: #5C5EE4;
  border-radius: 0.04rem;
  cursor: pointer;
}
/*  */
.ant-table-thead tr th {
  background: #F9F9FE;
  padding-top: 0.13rem !important;
  padding-bottom: 0.13rem !important;
  line-height: 1;
  background: #F9F8FD;
  font-size: 0.13rem;
  color: #9696A3;
  font-family: 'Source Han Sans CN';
}
.ant-table-cell::before {
  height: 0 !important;
}
.ant-table-tbody > tr > td {
  padding-top: 0;
  padding-bottom: 0;
  font-size: 0.14rem;
  color: #040E34;
  height: 0.6rem;
}
.opt-icon {
  width: 0.3rem;
  height: 0.3rem;
  line-height: 0.3rem;
  cursor: pointer;
  text-align: center;
  margin-bottom: 0;
  transition: all 0.3s;
  margin-left: -0.075rem;
  border-radius: 0.04rem;
}
.opt-icon:hover {
  background: #F2F0FE;
  color: #5C5EE4;
}
/* pop */
.com-opt-pop {
  position: relative;
}
.com-opt-pop .ant-popover {
  line-height: 0.33rem;
  font-size: 0.12rem;
  top: auto !important;
  bottom: -100%;
  padding-bottom: 0;
  min-width: 0.8rem;
  text-align: center;
}
.com-opt-pop .ant-popover .ant-popover-arrow {
  width: 0;
}
.com-opt-pop .ant-popover .ant-popover-inner {
  border-radius: 0.08rem;
  padding: 0.06rem 0.1rem;
}
.com-opt-pop .ant-popover .ant-popover-inner-content {
  padding: 0;
}
.com-opt-pop .ant-popover p {
  margin-bottom: 0;
  padding: 0 0.18rem;
  color: #5C5EE4;
  cursor: pointer;
}
.ant-select:hover {
  border-color: #5C5EE4 !important;
}
/* com-modal */
.com-modal .ant-modal-header {
  border-top-left-radius: 0.12rem;
  border-top-right-radius: 0.12rem;
  border-bottom: 0;
  font-size: 0.18rem;
  color: #040E34;
}
.com-modal .ant-form-item {
  transition: none !important;
}
.com-modal .ant-form-item label {
  height: 0.44rem;
  line-height: 0.44rem;
}
.com-modal .ant-input {
  line-height: 0.34rem;
  border-radius: 0.04rem;
  border-color: #D9DBE1;
}
.com-modal .ant-select-selection-placeholder,
.com-modal .ant-select-selection-item {
  line-height: 0.42rem !important;
}
.com-modal .ant-select-selector {
  line-height: 0.44rem !important;
  height: 0.44rem !important;
  border-radius: 0.04rem !important;
}
.com-modal .ant-modal-content {
  border-radius: 0.12rem;
}
.com-modal .f-btn {
  width: 100%;
  text-align: center;
  margin-bottom: 0;
}
.com-modal .submit {
  background: #5C5EE4;
  color: #fff;
}
.com-modal button {
  line-height: 0.4rem;
  height: 0.4rem;
  padding: 0 0.16rem;
  border-radius: 0.04rem;
  margin: 0 0.04rem;
  box-shadow: none !important;
}
.com-modal .ant-modal-header {
  padding: 0.4rem 0.3rem 0.3rem;
}
.com-modal .ant-modal-header .ant-modal-title {
  line-height: 1;
}
.com-modal textarea {
  resize: none;
}
.com-modal .ant-modal-body {
  width: 99%;
  margin: 0 auto;
  padding: 0 0.3rem 0.4rem 0.3rem;
}
/*  */
.swal2-container .swal2-popup {
  font-size: 16px;
}
.swal2-container .swal2-modal {
  border-radius: 0.2rem;
  width: 4.8rem;
}
.swal2-container .swal2-modal .swal2-confirm {
  line-height: 0.38rem;
  padding: 0 0.15rem;
  border-radius: 0.04rem;
  margin: 0 0.04rem;
  font-size: 0.14rem;
  box-shadow: none !important;
  box-sizing: border-box;
}
.swal2-container .swal2-modal .swal2-html-container {
  font-size: 0.14rem;
  color: #525666;
  white-space: pre-wrap;
}
.swal2-container .swal2-icon {
  width: 0.62rem !important;
  height: 0.62rem !important;
  margin: 0.3rem auto 0.22rem !important;
  border: 2px solid;
}
.swal2-container .swal2-icon.swal2-error [class^=swal2-x-mark-line] {
  top: 1.8em;
  width: 1.6em;
  height: 0.1em;
}
.swal2-container .swal2-icon.swal2-warning {
  box-sizing: border-box;
}
.swal2-container .swal2-warning {
  width: 0.64rem;
  height: 0.64rem;
  border-width: 2px;
}
.swal2-container .swal2-warning .swal2-icon-content {
  font-size: 2.2em !important;
}
.swal2-container .swal2-icon.swal2-success {
  width: 0.64rem;
  height: 0.64rem;
  border-color: #1BC5BD;
  color: transparent;
  border: 2px solid;
  box-sizing: border-box;
}
.swal2-container .swal2-icon.swal2-success [class^=swal2-success-line] {
  background-color: #1BC5BD;
  height: 0.1em;
}
.swal2-container .swal2-icon.swal2-success .swal2-success-ring {
  border: 2px solid rgba(27, 197, 189, 0.3);
  top: -0.1em;
  left: -0.1em;
}
.swal2-container .swal2-icon.swal2-success [class^=swal2-success-line][class$=long] {
  right: 0.55em;
  top: 1.8em;
  width: 1.8em;
}
.swal2-container .swal2-icon.swal2-success [class^=swal2-success-line][class$=tip] {
  left: 0.7em;
  top: 2.1em;
  width: 1em;
}
@media screen and (max-width: 1300px) {
  .com-opt-pop .ant-popover {
    min-width: 1rem;
  }
}
