.login {
  background: #fafafe;
  width: 100%;
  height: 100%;
  position: relative;
}
.login .l-bg {
  width: 2.17rem;
  height: 2.75rem;
  position: absolute;
  left: 0;
  bottom: 0;
  background: url(../../assets/images/bg-left.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 1;
}
.login .r-bg {
  width: 46.04%;
  max-width: 7rem;
  height: 60.9%;
  position: absolute;
  right: 4.74%;
  top: 19.9%;
  bottom: 19.9%;
  background: url(../../assets/images/bg-right.png);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 1;
}
.r-bg-color {
  width: 30%;
  max-width: 7rem;
  height: 100%;
  background: #f0f0fd;
  opacity: 0.6;
  border-radius: 0px;
  position: absolute;
  right: 0;
  bottom: 0;
}
.login .form {
  width: 5.3rem;
  // height: 5.38rem;
  position: absolute;
  padding: 0.5rem 0.4rem;
  top: 19.9%;
  left: 18.75%;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 0.2rem;
  box-shadow: 0 0 0.2rem 0.1rem rgba(105, 106, 230, 0.05);
  z-index: 10;
}
.login_title {
  font-size: 0.26rem;
  font-family: Source Han Sans CN;
  font-weight: bold;
  height: 0.3rem;
  line-height: 0.3rem;
  color: #040e34;
  margin-bottom: 0.1rem;
}
.login_subtitle span {
  height: 0.3rem;
  line-height: 0.3rem;
  font-size: 0.16rem;
  font-family: Source Han Sans CN;
  font-weight: 400;
  line-height: 0px;
  color: #9696a3;
}
.toregister {
  color: #5c5ee4 !important;
  cursor: pointer;
}
.item_select {
  border-right: 0.01rem solid #ccc;
}
.form_item {
  height: 0.48rem;
  line-height: 0.48rem;
  // padding: 0.07rem;
  margin: 0.2rem 0 0.2rem 0;
  background: #f3f3f9;
  opacity: 1;
  border-radius: 0.04rem;
  .ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    height: auto;
  }
}
.codephoneinput {
  margin-top: 0.4rem !important;
}
.ant-select-selector {
  box-shadow: none !important;
}
.ant-input:focus {
  box-shadow: none !important;
}
.passward {
  margin: 0 !important;
  position: relative;
  .pass_tip {
    position: absolute;
    bottom: -0.33rem;
    left: 0;
  }
}
.forget {
  color: #5c5ee4;
  cursor: pointer;
  margin-top: 0.2rem;
}
.errortip {
  color: #f64e60;
  font-size: 0.12rem;
}
.code_item {
  display: flex;
  justify-content: space-between;
  position: relative;
  .setcodebox {
    position: absolute;
    bottom: -0.2rem;
    left: 0;
  }
}

.form_item_code {
  width: 70%;
  height: 0.48rem;
  line-height: 0.48rem;
  // padding: 0.06rem;
  background: #f3f3f9;
  opacity: 1;
  border-radius: 0.04rem;
}
.form_item_code input {
  width: "100%";
  border: "none";
  outline: "none";
  background-color: "#f3f3f9";
  outline: none;
}
.bottom_code {
  width: 0.96rem;
  height: 0.48rem !important;
  background: #42c2fb !important;
  opacity: 1;
  border-radius: 0.04rem !important;
  color: #fff !important;
  white-space: nowrap !important;
  padding: 0 !important;
  span {
    white-space: normal;
  }
}

.showtab {
  display: block;
}
.closetab {
  display: none;
}
.form_item .ant-select > .ant-select-selector {
  border: none !important;
  outline: none;
  background-color: #f3f3f9 !important;
}
.form_item .ant-select-focused .ant-select-selection,
.ant-select-selection:focus,
.ant-select-selection:active {
  border-color: transparent !important;
  box-shadow: 0 0 0px !important;
}

.agreement .ant-checkbox-wrapper {
  font-size: 0.13rem;
  margin: 0.1rem 0;
}
.agreement_link {
  color: #5c5ee4;
}
.login_btn {
  width: 100%;
  height: 0.48rem;
  opacity: 1;
  border-radius: 0.04rem;
  text-align: center;
  line-height: 0.48rem;
  font-size: 0.16rem;
  cursor: pointer;
}
.phonelogin_btn:hover {
  opacity: 0.7;
}
.phonelogin_btn {
  width: 100%;
  height: 0.48rem;
  background: #5c5ee4;
  opacity: 1;
  border-radius: 0.04rem;
  color: #fff;
  text-align: center;
  line-height: 0.48rem;
  font-size: 0.16rem;
  margin-top: 0.3rem;
}
.userlogin_btn {
  background: #fff;
  opacity: 1;
  border-radius: 0.04rem;
  color: #5c5ee4;
  border: 1px solid #5c5ee4;
  margin-top: 0.15rem;
}
.userlogin_btn:hover {
  background-color: #5c5ee4;
  color: #fff;
}
.picCheck {
  top: 50% !important;
  left: 20%;
  margin: 0 !important;
  .ant-modal-content {
    border-radius: 0.12rem;
    .ant-modal-header {
      border-radius: 0.12rem;
      border-bottom: none;
      padding-bottom: 0;
    }
    .ant-modal-body {
      padding: 0.2rem;
    }
    .ant-modal-footer {
      border-top: none;
      padding: 0;
    }
  }
  .check_btn {
    width: 100%;
    height: 0.4rem;
    line-height: 0.4rem;
    text-align: center;
    background-color: #5c5ee4;
    border-radius: 0.04rem;
    color: #fff;
    margin-top: 0.25rem;
    cursor: pointer;
  }
  .outlined {
    color: #5c5ee4;
    font-size: 0.18rem;
    margin-top: 0.1rem;
  }
  .captcha {
    height: 0.4rem;
    width: 22%;
  }
  .captchabox {
    position: relative;
    .secaptchabox {
      position: absolute;
      bottom: -0.2rem;
      left: 0px;
    }
  }
}
.login_form {
  .ant-tabs-tab:hover {
    color: #000 !important;
  }
  .ant-tabs-tab-active {
    color: #000 !important;
    border-bottom-color: #5c5ee4 !important;
  }
  .ant-tabs-ink-bar {
    background-color: transparent !important;
  }
  .ant-tabs-tab.ant-tabs-tab-active .ant-tabs-tab-btn {
    color: #5c5ee4 !important;
  }
  .ant-tabs-top .ant-tabs-ink-bar-animated:before {
    content: " ";

    background-color: #5c5ee4;

    width: 100%;

    height: 0.02rem;

    position: absolute;

    margin: auto;
  }
  .ant-tabs-nav-list {
    width: 100%;
    .ant-tabs-tab {
      width: 100%;

      .ant-tabs-tab-btn {
        width: 100%;
        text-align: center;
      }
    }
  }
}

@primary-color: #5C5EE4;