.ant-layout-sider-children {
  overflow-y: auto;
}
.com-sider {
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: 5;
  background: rgba(20, 20, 51, 0.9);
}
.com-sider .side-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 200px;
  height: 100%;
  background-color: gray;
}
.com-sider .side-menu a {
  width: 100%;
  line-height: 40px;
  display: block;
  text-align: center;
  text-decoration: none;
  color: #fff;
}
.com-sider .logo {
  margin: 24px 24px 20px 24px;
}
.com-sider .logo img {
  height: 32px;
}
.com-sider .ant-menu-item,
.com-sider .ant-menu-submenu-title {
  padding-left: 0.24rem !important;
}
.com-sider .ant-menu-submenu-open {
  padding-bottom: 2.44rem;
}
.com-sider .ant-menu {
  background: none;
  position: relative;
}
.com-sider .ant-menu .ant-menu-inline.ant-menu-sub {
  background: none;
}
.com-sider .ant-menu .ant-menu-item.ant-menu-item-selected {
  background: rgba(255, 255, 255, 0.16) !important;
}
.com-sider .ant-menu img {
  width: 24px;
  height: 24px;
}
.com-sider .ant-menu-submenu {
  border-bottom: 1px solid rgba(110, 110, 184, 0.2);
}
@media screen and (max-width: 992px) {
  .com-sider {
    background: #2a2a47;
  }
}
